const ELEMENT_NODE = 1;

type NodeCollection =
  | {
      length: number;
      item?: (index: number) => Element | null;
      [index: number]: Element | undefined;
    }
  | ArrayLike<Element>;

/**
 * Normalizes a NodeList or HTMLCollection-like object into an array.
 *
 * @param {{ length?: number, item?: (index: number) => Node }|ArrayLike<Node>|null|undefined} collection
 * @returns {Element[]} List of nodes contained in the collection.
 */
/**
 * Converts a DOM collection into a plain array of elements.
 */
function collectionToArray(collection: NodeCollection | null | undefined): Element[] {
  const result: Element[] = [];
  if (!collection || typeof collection.length !== 'number') return result;
  for (let i = 0; i < collection.length; i += 1) {
    const item =
      typeof collection.item === 'function'
        ? collection.item(i)
        : (collection as Record<number, Element | undefined>)[i];
    if (item) {
      result.push(item);
    }
  }
  return result;
}

/**
 * Collects descendant elements whose local name matches the provided tag.
 *
 * @param {Element} node Root node to search.
 * @param {string|null|undefined} namespace Namespace URI that should match the element (optional).
 * @param {string} localName Target local name.
 * @returns {Element[]} Matching descendant elements.
 */
/**
 * Collects descendant elements matching the provided namespace/local name.
 */
export function getElementsByTagNameNs(
  node: Element | null | undefined,
  namespace: string | null | undefined,
  localName: string,
): Element[] {
  if (!node) return [];

  if (typeof node.getElementsByTagNameNS === 'function') {
    return collectionToArray(
      node.getElementsByTagNameNS(namespace ?? null, localName) as unknown as NodeCollection,
    );
  }

  if (typeof node.getElementsByTagName === 'function') {
    const matches = collectionToArray(
      node.getElementsByTagName(localName) as unknown as NodeCollection,
    );
    if (matches.length) {
      return matches;
    }
  }

  const matches: Element[] = [];
  const stack: Element[] = [node];
  while (stack.length) {
    const current = stack.pop();
    if (!current) continue;
    const childNodes = current.childNodes;
    if (!childNodes || typeof childNodes.length !== 'number') continue;
    for (let i = childNodes.length - 1; i >= 0; i -= 1) {
      const child = typeof childNodes.item === 'function' ? childNodes.item(i) : childNodes[i];
      if (!child || child.nodeType !== ELEMENT_NODE) continue;
      const elementChild = child as Element;
      const childLocalNameRaw = elementChild.localName || elementChild.nodeName || elementChild.tagName || '';
      const childLocalName = childLocalNameRaw.includes(':')
        ? childLocalNameRaw.split(':').pop()
        : childLocalNameRaw;
      const namespaceMatches =
        !namespace || !elementChild.namespaceURI || elementChild.namespaceURI === namespace;
      if (childLocalName === localName && namespaceMatches) {
        matches.push(elementChild);
      }
      stack.push(elementChild);
    }
  }

  return matches;
}

/**
 * Extracts direct child elements from the provided node.
 *
 * @param {Element} node Parent node.
 * @returns {Element[]} Array of child elements.
 */
/**
 * Returns the element-only immediate children for a node.
 */
export function getChildElements(node: Element | null | undefined): Element[] {
  const children: Element[] = [];
  const childNodes = node?.childNodes;
  if (!childNodes || typeof childNodes.length !== 'number') return children;
  for (let i = 0; i < childNodes.length; i += 1) {
    const child = typeof childNodes.item === 'function' ? childNodes.item(i) : childNodes[i];
    if (child && child.nodeType === ELEMENT_NODE) {
      children.push(child as Element);
    }
  }
  return children;
}
