import presetShapesData from './generated/preset-shapes.ts';
import {
  DIMENSION_SENSITIVE_SHAPES,
  rawDefinitions,
} from './generated/dimension-sensitive-definitions.ts';
import { convertPresetShapes } from './converter.ts';
import { DRAWINGML_NS } from './constants.ts';
import type { SvgPathDefinition, SvgPathStyle } from './types.ts';

const STYLE_KEYS: (keyof SvgPathStyle)[] = ['fill', 'stroke', 'strokeWidth', 'fillRule', 'clipRule'];

interface StoredPresetShape {
  viewBox: string;
  paths: SvgPathDefinition[];
}

const PRESET_SHAPES = presetShapesData as unknown as Record<string, StoredPresetShape>;

export interface PresetShape {
  preset: string;
  viewBox: string;
  paths: SvgPathDefinition[];
}

export type PresetStyleOverrideInput =
  | SvgPathStyle
  | SvgPathStyle[]
  | ((path: SvgPathDefinition, index: number) => SvgPathStyle | null | undefined);

export interface PresetShapeOptions {
  preset: string;
  styleOverrides?: PresetStyleOverrideInput;
  /** Target width for dimension-sensitive shapes. */
  width?: number;
  /** Target height for dimension-sensitive shapes. */
  height?: number;
}

/** Picks only supported style keys from a candidate override. */
function filterStyleOverrides(overrides: SvgPathStyle | null | undefined): SvgPathStyle | null {
  if (!overrides) return null;
  const filtered: SvgPathStyle = {};
  STYLE_KEYS.forEach((key) => {
    const value = overrides[key];
    if (value === undefined) return;
    if (key === 'strokeWidth') {
      if (typeof value === 'number') {
        filtered.strokeWidth = value;
      } else if (typeof value === 'string') {
        const numeric = Number(value);
        if (Number.isFinite(numeric)) {
          filtered.strokeWidth = numeric;
        }
      }
    } else if (typeof value === 'string') {
      filtered[key] = value;
    }
  });
  return Object.keys(filtered).length ? filtered : null;
}

/**
 * Applies caller-specified style overrides to the converted path list.
 * The data file is treated as immutable, so we always clone the path objects first.
 */
function applyStyleOverrides(
  basePaths: SvgPathDefinition[],
  overrides?: PresetStyleOverrideInput,
): SvgPathDefinition[] {
  const resolved = basePaths.map((path) => ({ ...path }));
  if (!overrides) return resolved;

  const resolveOverrides = (path: SvgPathDefinition, index: number): SvgPathStyle | null => {
    if (typeof overrides === 'function') {
      return filterStyleOverrides(overrides(path, index));
    }
    if (Array.isArray(overrides)) {
      return filterStyleOverrides(overrides[index]);
    }
    return filterStyleOverrides(overrides);
  };

  return resolved.map((path, index) => {
    const extras = resolveOverrides(path, index);
    if (!extras) return path;
    return { ...path, ...extras };
  });
}

/** Lists the preset names that have been pre-generated. */
export function listPresetNames(): string[] {
  return Object.keys(PRESET_SHAPES);
}

/**
 * Generates a shape on-demand from raw XML definition with specific dimensions.
 * Used for dimension-sensitive shapes that need non-square aspect ratios.
 */
function generateOnDemand(
  preset: string,
  width: number,
  height: number,
): { viewBox: string; paths: SvgPathDefinition[] } | null {
  const rawXml = rawDefinitions[preset];
  if (!rawXml) return null;

  const wrappedXml = `<presetRoot xmlns:a="${DRAWINGML_NS}">${rawXml}</presetRoot>`;
  const [shape] = convertPresetShapes(wrappedXml, { width, height });
  if (!shape) return null;

  return {
    viewBox: shape.viewBox,
    paths: shape.paths,
  };
}

/**
 * Produces a preset shape, using on-demand generation for dimension-sensitive shapes
 * when width and height are provided with non-square aspect ratios.
 */
export function createPresetShape(options: PresetShapeOptions): PresetShape {
  const { preset, styleOverrides, width, height } = options;
  if (!preset) {
    throw new Error('createPresetShape requires a preset name.');
  }

  // Check if this shape needs on-demand generation
  const needsOnDemand =
    DIMENSION_SENSITIVE_SHAPES.has(preset) &&
    width != null &&
    height != null &&
    width !== height;

  if (needsOnDemand) {
    const generated = generateOnDemand(preset, width, height);
    if (generated) {
      return {
        preset,
        viewBox: generated.viewBox,
        paths: applyStyleOverrides(generated.paths, styleOverrides),
      };
    }
  }

  // Fast path: use pre-generated shape
  const stored = PRESET_SHAPES[preset];
  if (!stored) {
    throw new Error(`Unknown preset shape: ${preset}`);
  }

  return {
    preset,
    viewBox: stored.viewBox,
    paths: applyStyleOverrides(stored.paths, styleOverrides),
  };
}

/** Returns the serialized SVG element for a preset using the same options as `createPresetShape`. */
export function getPresetShapeSvg(options: PresetShapeOptions): string {
  const shape = createPresetShape(options);
  const pathMarkup = shape.paths
    .map((path) => {
      const attributes: string[] = [`d="${path.d}"`];
      if (path.fill !== undefined) {
        attributes.push(`fill="${path.fill}"`);
      }
      if (path.stroke !== undefined) {
        attributes.push(`stroke="${path.stroke}"`);
      }
      if (path.strokeWidth !== undefined) {
        attributes.push(`stroke-width="${path.strokeWidth}"`);
      }
      if (path.fillRule !== undefined) {
        attributes.push(`fill-rule="${path.fillRule}"`);
      }
      if (path.clipRule !== undefined) {
        attributes.push(`clip-rule="${path.clipRule}"`);
      }
      return `  <path ${attributes.join(' ')} />`;
    })
    .join('\n');

  return `<svg xmlns="http://www.w3.org/2000/svg" viewBox="${shape.viewBox}" preserveAspectRatio="none">\n${pathMarkup}\n</svg>`;
}
