/**
 * Lookup table for preset DrawingML fill keywords mapped to CSS expressions.
 */
const PRESET_SHADES: Record<string, string> = {
  darken: 'color-mix(in srgb, currentColor 60%, black)',
  darkenless: 'color-mix(in srgb, currentColor 80%, black)',
  lighten: 'color-mix(in srgb, currentColor 60%, white)',
  lightenless: 'color-mix(in srgb, currentColor 80%, white)',
  accent: 'var(--preset-accent, currentColor)',
  accentlight: 'color-mix(in srgb, currentColor 85%, white)',
};

/**
 * Converts a DrawingML fill attribute into a CSS-compatible fill value.
 *
 * @param attr Raw fill attribute value.
 * @returns CSS fill value.
 */
export function resolveFill(attr: string | null | undefined): string {
  if (attr == null) return '#000000';
  const normalized = String(attr).toLowerCase();
  if (normalized === 'norm' || normalized === 'auto') return '#000000';
  if (normalized === 'none' || normalized === 'false' || normalized === 'transparent') {
    return 'none';
  }
  if (normalized in PRESET_SHADES) {
    return PRESET_SHADES[normalized];
  }
  if (/^#|^rgb|^hsl|^var\(/i.test(attr)) return attr;
  return '#000000';
}

/**
 * Converts a DrawingML stroke attribute into a CSS-compatible stroke value.
 *
 * @param attr Raw stroke attribute value.
 * @returns CSS stroke value.
 */
export function resolveStroke(attr: string | null | undefined): string {
  if (attr == null) return '#000000';
  const normalized = attr.toLowerCase();
  if (normalized === 'none' || normalized === 'false') return 'none';
  if (/^#|^rgb|^hsl|^var\(/i.test(attr)) return attr;
  return '#000000';
}
