/** Namespace used throughout DrawingML preset geometry. */
export const DRAWINGML_NS = 'http://schemas.openxmlformats.org/drawingml/2006/main';
/** Angle constant representing an entire circle in DrawingML units. */
export const FULL_CIRCLE = 21600000;
/** Half of {@link FULL_CIRCLE}. */
export const HALF_CIRCLE = FULL_CIRCLE / 2;
/** Number of DrawingML angle units per degree (60000). */
export const ANGLE_UNITS = 60000;
